/* Variables */
#Container .items-container {
  /* Safari */
}
#Container .items-container .loader-wrap {
  width: 74px;
  margin: 100px auto;
}
#Container .items-container .loader-wrap .loader {
  border: 12px solid #f3f3f3;
  border-radius: 50%;
  border-top: 12px solid #8c8c8c;
  width: 50px;
  height: 50px;
  -webkit-animation: spin 2s linear infinite;
  /* Safari */
  animation: spin 2s linear infinite;
  -webkit-filter: drop-shadow(0.5px 0.5px 10px rgba(0, 0, 0, 0.1));
  filter: drop-shadow(0.5px 0.5px 10px rgba(0, 0, 0, 0.1));
}
@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
  }
}
@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
#Container .items-container .items-wrapper {
  overflow: auto;
  width: 100%;
  margin: 0 auto;
}
#Container .items-container .items-wrapper .item {
  width: 46%;
  margin: 1% 2%;
  height: 24vmax;
  min-height: 150px;
  float: left;
  overflow: hidden;
  position: relative;
}
#Container .items-container .items-wrapper .item .items-mask {
  background: -moz-linear-gradient(top, rgba(0, 0, 0, 0.85) 0%, rgba(0, 0, 0, 0) 50%);
  /* FF3.6-15 */
  background: -webkit-linear-gradient(top, rgba(0, 0, 0, 0.85) 0%, rgba(0, 0, 0, 0) 50%);
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to top, rgba(0, 0, 0, 0.85) 0%, rgba(0, 0, 0, 0) 50%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#a6000000', endColorstr='#00000000', GradientType=0);
  /* IE6-9 */
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  color: #fff;
  z-index: 1;
  text-align: center;
  transition: all 0.3s ease-in-out;
}
#Container .items-container .items-wrapper .item .city {
  z-index: 1;
  position: absolute;
  color: #fff;
  font-size: 15px;
  left: 0;
  top: 0;
  background-color: #DC8F00;
  padding: 2px 4px;
}
#Container .items-container .items-wrapper .item .food-info {
  z-index: 1;
  position: absolute;
  bottom: 0px;
  text-align: left;
  color: #fff;
  padding: 8px;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.5);
}
#Container .items-container .items-wrapper .item .food-info .town {
  font-style: italic;
  font-size: 15px;
}
#Container .items-container .items-wrapper .item .food-info .hostWords {
  max-height: 0px;
  overflow: hidden;
  transition: 0.3s;
  font-size: 15px;
}
#Container .items-container .items-wrapper .item .food-info .hostWords hr {
  width: 4vw;
  border: 3px solid #fff;
  margin-left: 0;
}
#Container .items-container .items-wrapper .item img {
  display: block;
  width: 100%;
  height: auto;
}
#Container .items-container .items-wrapper .item:hover {
  cursor: pointer;
}
#Container .items-container .items-wrapper .item:hover .hostWords {
  max-height: 66px;
}
@media (min-width: 768px) {
  #Container .items-container .items-wrapper {
    width: 90%;
  }
  #Container .items-container .items-wrapper .item {
    width: 31%;
    margin: 1% 1%;
    height: 17vmax;
    min-height: unset;
  }
}
.select-fields {
  background: #fff;
}
.select-fields .fields {
  border: none;
  height: 50px;
  line-height: 50px;
  padding: 10px 0 0 0;
  width: 100%;
  margin: 0 auto;
}
.select-fields .fields .dropdown-container {
  width: 45vw;
  margin: 0 10px;
  float: left;
  cursor: pointer;
  position: relative;
}
.select-fields .fields .dropdown-container .dropdown-display {
  background-color: #eee;
  height: 40px;
  line-height: 40px;
  text-align: left;
}
.select-fields .fields .dropdown-container .dropdown-display span {
  padding-left: 20px;
}
.select-fields .fields .dropdown-container .dropdown-display i.angle-down {
  display: inline-block;
  position: absolute;
  width: 7px;
  height: 7px;
  background: transparent;
  text-indent: -9999px;
  border-top: 2px solid #bfbfbf;
  border-left: 2px solid #bfbfbf;
  transition: all 250ms ease-in-out;
  text-decoration: none;
  color: transparent;
  transform: rotate(-135deg);
  right: 13px;
  top: 15px;
}
.select-fields .fields .dropdown-container .dropdown-list {
  z-index: 9999;
  position: relative;
  background-color: #fff;
  top: -17px;
}
.select-fields .fields .dropdown-container .dropdown-list ul {
  list-style: none;
  list-style-type: none;
  width: 100%;
  padding: 0;
  font-size: 14px;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  border-top: none;
}
.select-fields .fields .dropdown-container .dropdown-list ul li {
  text-align: left;
}
.select-fields .fields .dropdown-container .dropdown-list ul li a {
  width: 100%;
  display: block;
  text-decoration: none;
  color: #151515;
  text-align: left;
}
.select-fields .fields .dropdown-container .dropdown-list ul li a span {
  padding-left: 20px;
}
.select-fields .fields .dropdown-container .dropdown-list ul li a:hover {
  background: #f5f5f5;
}
@media (min-width: 768px) {
  .select-fields .fields {
    width: 540px;
  }
  .select-fields .fields .dropdown-container {
    width: 250px;
  }
}
#wrapper {
  text-align: center;
}
#wrapper h1 {
  color: #717171;
}
